body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
img{
  position: absolute;
}
.header{
  background: rgb(6, 192, 173);
  height: 50px;
}
.Nam{
  position: relative;
  padding-left: 100px;
  color: white;
}
.searc{
  float: right;
  padding: 6px;
  border: none;
  margin-top: 8px;
  margin-right: 16px;
  font-size: 17px;

}
.footr{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;  
  text-align: center;
  text-align:center;
}

.main-container {
  padding: 30px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  float: right;
  margin: 20px 20% 20px 33%;
  padding: 20px;
  width: 500px;
  min-height: auto;
  grid-template-rows: 20px 50px 1fr 50px;
  border-radius: 10px;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.2s;
}

.card:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}

.card__title {
  font-weight: 100px;
  color:  #000000;
}
.card__title{
  margin: 10px 20px 10px 100px;
  width: 50%;
  height: 30px;
}
.cardco{
  margin: 10px;
  width: 90%;
  height: 95px;
}
.card-1 {
  background: radial-gradient(#1fe4f5, #3fbafe);
}
 
.plus-sing{
  float: left ;
}

.note{
  float: left;
   
}
.note{
  margin: 20px;
  padding: 20px;
  width: 300px;
  min-height: 150px;
  display: grid;
  grid-template-rows: 20px 50px 1fr 50px;
  border-radius: 10px;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.2s;
}
.note{
  background: radial-gradient(#60efbc, #58d5c9);
}